<template>
    <img  @click.native="$say('clicked:IMAGE'),data.size=data.size=='contain'?'cover':'contain'" :class="[ 'IMAGE-BASIC', 'ROOT', params.level,params.wrap]" :src="data.url"
    :data-image-size="data.size||params.size||'contain'"
     />
</template>
<style lang="less">
img.IMAGE-BASIC
{
    
    width: 100%;
    height: 100%;
    &[data-image-size="contain"]
    {
        object-fit: contain;
    }
    &[data-image-size="cover"]
    {
        object-fit: cover;
    }
}
</style>

<script>
/**
 * params={
 *      level:[h1,h2,h3,h4,h5,h6],
 *      wrap:[wrap,breakword]
 * }
 * data.IMAGE=String
 */
import v from '../validators.js';
var {vURL,vNumber,vString,vPromise,vObject,vRequired,$OR,$AND}=v;
export default {
    name:"IMAGE-BASIC",
    props:['data'],
    fields:{
        data:{
            is:/^IMAGE.*/,
            url:vURL
        }
    }
}
</script>



